<template>
  <div>
  <transition name="list">
   <div class="playlistBody" v-show="showList">
    <div class="playlist-cover" @click="_close"></div>
    <div class="playlist-content">
      <div class="playlist-header">
        <div class="control-btn">
          <i class="iconfont control-icon-min" :class="iconMode" @touchstart.stop="_changeMode()"></i>
        </div>
        <div class="playlist-mode">{{ mode === 0 ? '' : mode === 1 ? '' : '' }}</div>
        <div class="delete-all">
          <i class="iconfont delete-icon icon-105" @touchstart.stop="_deleteAll()"></i>
        </div>
      </div>
      <div class="playlist-body">
        <div class="playlist-body-list">
          <search-list :searches="sequenceList" :searcheIndex="listIndex" @select="_select" @delete="_delete" ref="searchlist"></search-list>
        </div>
        <div class="playlist-add" @click="_openAddList"></div>
        <i class="iconfont icon-dingwei locate-icon" @click="_loction"></i>
      </div>
      <div class="playlist-footer" @click="_close"></div>
    </div>
  </div>
  </transition>
<add-list v-show="openAddList" @close="_closeAddList"></add-list>
</div>
</template>
<script type="text/javascript">
  import SearchList from 'base/searchList/searchList'
  import {mapGetters, mapMutations, mapActions} from 'vuex'
  import {playMode} from 'common/js/config'
  import {playerMixin} from 'common/js/mixin'
  import AddList from 'components/playlist/addlist'
  // mapActions
  export default {
    mixins: [playerMixin],
    data() {
      return {
        myList: [],
        showList: false,
        listIndex: 0,
        openAddList: false
      }
    },
    components: {
      SearchList,
      AddList
    },
    created() {
      // console.log(this.playlist)
      // document.addEventListener('touchmove', this._preventDefault, { passive: false });
      // console.log(this.sequenceList)
      // this.myList = this._normalList(this.sequenceList)
    },
    computed: {
      // iconMode() {
      //   return this.mode === playMode.sequence ? 'icon-shunxuxunhuan' : this.mode === playMode.loop ? 'icon-danquxunhuan' : 'icon-suijibofang'
      // },
      ...mapGetters([
        'playing',
        'currentIndex'
      ])
    },
    methods: {
      _normalList(list) {
        let ret = []
        list.forEach((item) => {
          const itemInfo = `${item.name}-${item.singer}`
          ret.push(itemInfo)
        })
        return ret
      },
      _select(item, index) {
        console.log(item, index)
        if (item.id === this.currentSong.id) {
          this.setPlayingState(!this.playing)
          return
        }
        if (this.mode === playMode.random) {
          const itemIndex = this.playlist.findIndex((e) => {
            return e.id === item.id
          })
          this.setCurrentIndex(itemIndex)
        } else {
          this.setCurrentIndex(index)
        }
        // this.$refs.searchlist._loction()
        if (!this.playing) {
          this.setPlayingState(true)
        }
      },
      _loction() {
        this.$refs.searchlist._loction()
      },
      _openAddList() {
        this.openAddList = true
      },
      _closeAddList() {
        this.openAddList = false
      },
      _delete(item, index) {
        this.deleteSong(item)
      },
      _close() {
        this.showList = !this.showList
      },
      _deleteAll() {
        this.deleteSongList()
      },
      ...mapMutations({
        setPlaylist: 'SET_PLAYLIST',
        setPlayingState: 'SET_PLAYING_STATE'
      }),
      ...mapActions([
        'deleteSong',
        'deleteSongList'
      ])
    },
    watch: {
      currentIndex() {
        this.$refs.searchlist._loction()
        if (this.mode === playMode.random) {
          const index = this.sequenceList.findIndex((e) => {
            return e.id === this.currentSong.id
          })
          this.listIndex = index
          console.log(this.listIndex)
        } else {
          this.listIndex = this.currentIndex
          console.log(this.listIndex)
        }
      }
    }
  }
</script>
<style scoped>
  .list-enter-active, .list-leave-active{
    transition: all .3s;
    transform: translate3d(0,0,0);
    opacity: 1;
  }
  .list-enter, .list-leave-to{
    transition: all .3s;
    transform: translate3d(0,100%,0);
    opacity: 0;
  }
  .playlistBody{
    position: fixed;
    top: 0;
    bottom: 0px;
    left: 0;
    right: 0;
    z-index: 15;
    touch-action: none;
  }
  .playlist-cover{
    position: relative;
    top: 0;
    width: 100%;
    height: 25%;
    background: #111;
    opacity: .1;
  }
  .playlist-content{
    position: relative;
    bottom: 0;
    width: 100%;
    height: 75%;
    background: #333;
  }
  .playlist-header{
    width:100%;
    max-height: 10%;
    height: 50px;
    line-height: 50px;
    text-align: center;
    position: absolute;
    top: 0;
  }
  .playlist-mode{
    width: 60px;
    float: left;
  }
  .delete-all{
    width: 50px;
    float: right;
  }
  .control-btn{
    width: 60px;
    float: left;
  }
  .playlist-body{
   height: 80%;
   width:100%;
   position: absolute;
   top: 10%;
   /*touch-action: none;*/
 }
 .playlist-footer{
  position: absolute;
  bottom: 0;
  max-height: 10%;
  height: 50px;
  line-height: 50px;
  width:100%;
  background: #222;
  font-size: 15px;
  color: hsla(0,0%,100%,.6);
  text-align: center;
}
.playlist-body-list{
  overflow: hidden;
  position: absolute;
  top: 0;
  bottom: 50px;
  left: 0;
  right: 0;
}
.playlist-add{
  position: absolute;
  left: 50%;
  bottom: 10px;
  height: 30px;
  line-height: 30px;
  width: 130px;
  color: hsla(0,0%,100%,.5);
  font-size: 12px;
  border: 1px solid #999;
  border-radius: 15px;
  text-align: center;
  transform: translate(-50%, 0);
}
.locate-icon{
  display: block;
  position: absolute;
  bottom: 5px;
  right: 10px;
  width: 40px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  color: rgba(255,205,49,.5);
  font-size: 18px;
}
.control-icon-min{
  font-size: 25px;
  color: rgba(255,205,49,.5);
}
.delete-icon{
 font-size: 15px;
 color: hsla(0,0%,100%,.3);
}
.playlist-mode{
  font-size: 14px;
  color: #999;
}
</style>


