<template>
  <transition name="add">
  <div class="addListBody">
    <header class="add-list-header">
    <span class="add-title"></span>
    <span class="iconfont icon-quxiao1 icon-close" @click="_close"></span>
    <div class="search-box-recently">
        <div class="box-recently-item" :class="{'recently-active':choose}" @click="_choose($event)" data-number="0"></div>
        <div class="box-recently-item" :class="{'recently-active':!choose}" @click="_choose($event)" data-number="1"></div>
    </div>
  </header>
    <search :recentlyPlayed="true" v-show="choose"></search>
    <div class="add-histroy" v-show="!choose">
      <!-- <search-list :searches="playHistory"></search-list> -->
      <v-scroll>
      <ul class="add-histroy-ul">
        <li v-for="(item, index) in playHistory" class="add-histroy-li ellipsis" @click="_insertSong(item, index)">
        <div class="histroy-li-item-n">{{item.name}}</div>
        <div class="histroy-li-item-s">{{item.album}} - {{item.singer}}</div>
        </li>
      </ul>
      </v-scroll>
    </div>
    <top-tips ref="toptips">
      <span class="top-tips-text"></span>
    </top-tips>
  </div>
</transition>
</template>
<script type="text/javascript">
import search from 'components/search/search'
import VScroll from 'base/scroll/scroll'
import Song from 'common/js/song'
import {mapGetters, mapActions} from 'vuex'
import topTips from 'base/topTips/toptips'

export default{
  data() {
    return {
      choose: true
    }
  },
  created() {
  },
  computed: {
    ...mapGetters([
      'playHistory'
    ])
  },
  mounted() {
  },
  methods: {
    _close() {
      this.$emit('close')
    },
    _choose(e) {
      console.log()
      if (parseInt(e.target.getAttribute('data-number')) === 1) {
        this.choose = false
        return
      }
      this.choose = true
    },
    _insertSong(item, index) {
      if (item !== 0) {
        this.insertSong(new Song(item))
        this.$refs.toptips._open()
      }
    },
    ...mapActions([
      'insertSong'
    ])
  },
  components: {
    search,
    VScroll,
    topTips
  }
}
</script>
<style scoped>
/*  addlist*/
  .add-enter-active, .add-leave-active{
    transition: all .3s;
    transform: translate3d(0,0,0);
    opacity: 1;
  }
  .add-enter, .add-leave-to{
    transition: all .3s;
    transform: translate3d(100%,0,0);
    opacity: 0;
  }
  .addListBody{
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 20;
    background: #222;
  }
  .add-list-header{
    position: relative;
    z-index: 20;
    height: 88px;
    width: 100%;
    line-height: 50px;
    font-size: 16px;
    color: #fff;
    background: #222;
    text-align: center;
    margin-bottom: 20px;
    /*overflow: hidden;*/
  }
  .add-title{
    /*float: right;*/
    width: 90%;
    height: 50px;
  }
  .icon-close{
    position: absolute;
    right: 10px;
    /*float: right;*/
   /* margin-left: 40%;*/
    width: 10%;
    height: 50px;
    font-size: 18px;
    color: solid hsla(0,0%,100%,.3);
  }
  .search-box-recently {
    width: 90%;
    height: 32px;
    line-height: 32px;
    text-align: center;
    color: #999;
    font-size: 14px;
    border-radius: 5px;
    margin: 0 auto;
    border: 1px solid hsla(0,0%,100%,.1);
  }
  .box-recently-item {
    float: left;
    text-align: center;
    width: 50%;
    box-sizing: border-box;
  }
  .box-recently-item:first-child{
    border-right: 1px solid hsla(0,0%,100%,.1);
  }
  .search-box-recently .recently-active {
    background: #333;
    color: #fff;
  }
  .add-histroy{
    position: absolute;
    top: 98px;
    left: 0;
    right: 0;
    bottom: 0;
  }
  .add-histroy-ul{
    width: 100%;
  }
  .add-histroy-li{
    width: 100%;
    height: 70px;
    /*line-height: 50px;*/
    text-align: left;
    text-indent: 20px;
    font-size: 14px;
  }
  .histroy-li-item-n{
    height: 40px;
    line-height: 40px;
    font-size: 15px;
    color: #fff;
  }
  .histroy-li-item-s{
    height: 30px;
    line-height: 10px;
    font-size: 12px;
    color: hsla(0,0%,100%,.3);
  }
  .top-tips-text{
    display: inline-block;
    width: 100%;
    height: 40px;
    line-height: 40px;
    text-align: center;
    font-size: 14px;
    color: hsla(0,0%,100%,.5);
  }
</style>



