<template>
  <ol class="suggestOl">
    <li v-for="item in result" class="suggestLi" @click.stop="selectItem(item)">
      <i class="iconfont icon-yinle1 suggest-icon" v-show="!item.type"></i>
      <i class="iconfont icon-geshou suggest-icon" v-show="item.type"></i>
      <span v-html="item.singername || item.name + ' - ' + item.singer" class="suggest-text ellipsis"></span>
    </li>
  </ol>
</template>
<script type="text/javascript">
  import { search } from 'api/search'
  import { ERR_OK } from 'api/config'
  import {createSong} from 'common/js/song'
  import Singer from 'common/js/singer'
  import {mapMutations, mapActions} from 'vuex'

  export default {
    props: {
      query: {
        type: String,
        default: ''
      },
      showSinger: {
        type: Boolean,
        default: true
      }
    },
    data() {
      return {
        page: 1,
        result: [],
        hasMore: true
      }
    },
    methods: {
      _search() {
        if (!this.hasMore) {
          return
        }
        search(this.query, this.page, this.showSinger, 20).then((res) => {
          if (res.code === ERR_OK) {
            if (res.data.song.list.length < 20) {
              this.hasMore = false
              this.$emit('noMore', true)
            }
            this.result = this._genResult(res.data)
          }
        })
      },
      _searchMore() {
        if (!this.hasMore) {
          return
        }
        this.page++
        search(this.query, this.page, this.showSinger, 20).then((res) => {
          if (res.code === ERR_OK) {
            const song = res.data.song
            if (!song.list.length || (song.curpage - 1) * 20 > song.totalnum) {
              this.hasMore = false
              this.$emit('noMore', true)
              return
            }
            this.result = this.result.concat(this._genResult(res.data))
            // console.log(this.result)
          }
        })
      },
      _genResult(data) {
        let ret = []
        if (data.zhida.singermid) {
          ret.push(data.zhida)
        }
        if (data.song.list.length > 0) {
          ret = ret.concat(this._normalizeSongs(data.song.list))
        }
        // if (data.zhida.singerid) {
        //   ret.push({...data.zhida, ...{type: TYPE_SINGER}})
        // }
        // if (data.song) {
        //   ret = ret.concat(this._normalizeSongs(data.song.list))
        // }
        return ret
      },
      _normalizeSongs(list) {
        let ret = []
        list.forEach((musicData) => {
          if (musicData.songid && musicData.albumid) {
            ret.push(createSong(musicData))
          }
        })
        return ret
      },
      selectItem(item) {
        // console.log(item.type)
        if (item.type) {
          const singer = new Singer({
            id: item.singermid,
            name: item.singername
          })
          this.$router.push({
            path: `/search/${singer.id}`
          })
          this.setSinger(singer)
        } else {
          this.insertSong(item)
        }
        this.$emit('select', this.query)
        // localStorage.setItem('myCat', 'Tom')
      },
      ...mapMutations({
        setSinger: 'SET_SINGER'
      }),
      ...mapActions([
        'insertSong'
      ])
    },
    watch: {
      query() {
        this.hadPage = 15
        this.hasMore = true
        this.$emit('noMore', false)
        this._search()
      }
    }
  }
</script>
<style scoped>
  .suggestOl{
    width: 100%;
  }
  .suggestLi{
    width: 100%;
    height: 16px;
    padding-bottom: 20px;
    line-height: 16px;
    font-size: 14px;
    color: hsla(0,0%,100%,.3);
    text-align: center;
  }
  .suggest-icon{
    display: inline-block;
    width: 8%; 
    font-size: 18px;
    text-align: center;
  }
  .suggest-text{
    display: inline-block;
    width: 80%;
    text-align: left;
    text-indent: 10px;
  }
</style>


