<template>
<div>
  <div class="searchBody" ref="searchBody">
    <div class="search-box-postion">
      <search-box ref="searchBox" v-on:query="_changeQuery" :placeholder="placeholder"></search-box>
      <div class="search-hot-box" v-show="!searchQuery">
        <h1 class="search-hot-title"></h1>
        <div class="hot-key" v-for="item in HotKey" v-if="HotKey.length > 0" @click="_hotkeyInput(item)">{{ item.k }}</div>
        <div class="no-hotkey" v-if="HotKey.length <= 0"></div>
      </div>
      <div class="search-history-box" v-show="!searchQuery">
        <h1 class="search-history-title"><span class="iconfont icon-105 history-icon" @click="showTips" v-show="searchHistory.length"></span></h1>
        <div class="search-history-tips" v-show="!searchHistory.length"></div>
        <search-list :searches="searchHistory" class="search-history-list" @select="_changeQuery" @delete="deleteSearchHistory"></search-list>
      </div>
      <div class="search-query-box" v-show="searchQuery">
        <v-scroll v-on:updateY="_searchUpdateY" ref="scroll" v-on:beforeScroll="_listScroll">
          <suggest :query="searchQuery" ref="suggest" v-on:noMore="_noMore" v-on:select="_savesearch" :showSinger="!recentlyPlayed"></suggest>
          <div class="refresh-box" v-show="!noMore && !canUpated"></div>
          <div class="refresh-box" v-show="canUpated && !noMore">...</div>
          <div class="refresh-box" v-show="noMore"></div>
        </v-scroll>
      </div>
    </div>
  </div>
  <center-tips @sure="clearSearchHistory" ref="tips">
    <span class="tips-text"></span>
  </center-tips>
  <router-view></router-view>
  </div>
</template>
<script>
  import searchBox from 'base/searchBox/searchBox'
  import {getHotKey} from 'api/search'
  import {ERR_OK} from 'api/config'
  import suggest from 'components/suggest/suggest'
  import VScroll from 'base/scroll/scroll'
  import {mapActions, mapGetters} from 'vuex'
  import SearchList from 'base/searchList/searchList'
  import centerTips from 'base/centerTips/centertips'

  const PULL_DOWN_HEIGHT = 60

  export default{
    props: {
      // position: {
      //   type: Number,
      //   default: 88
      // },
      recentlyPlayed: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        HotKey: [],
        searchQuery: '',
        canUpated: false,
        noMore: false
      }
    },
    components: {
      searchBox,
      suggest,
      VScroll,
      SearchList,
      centerTips
    },
    computed: {
      placeholder() {
        return this.recentlyPlayed ? '' : ','
      },
      ...mapGetters([
        'searchHistory'
      ])
    },
    created() {
      this._getHotKey()
      // this.$nextTick(() => {
      //   this._setPostion()
      // })
    },
    methods: {
      _getHotKey() {
        getHotKey().then((res) => {
          if (res.code === ERR_OK) {
            this.HotKey = res.data.hotkey.slice(0, 13)
          }
        })
      },
      _hotkeyInput(item) {
        this.$refs.searchBox.setQuery(item.k)
      },
      // _setPostion() {
      //   this.$refs.searchBody.style.top = `${this.position}px`
      // },
      _changeQuery(res) {
        this.searchQuery = res
        this.$refs.searchBox.setQuery(res)
        this.$refs.scroll._slide(0, 0, true, false)
      },
      _searchUpdateY(res) {
        if (this.noMore) {
          return
        }
        if (res < 0) {
          if (res >= this.$refs.scroll.differH && this.canUpated) {
            this.$refs.suggest._searchMore()
            this.canUpated = false
          }
          if (res < this.$refs.scroll.differH - PULL_DOWN_HEIGHT && !this.canUpated) {
            // console.log('')
            this.canUpated = true
          }
        }
      },
      _noMore(res) {
        if (res) {
          this.noMore = true
        } else {
          this.noMore = false
        }
      },
      _listScroll() {
        // console.log(this.searchHistory)
        this.$refs.searchBox._blur()
      },
      _savesearch(res) {
        // console.log(this.searchHistory)
        this.saveSearchHistory(res)
      },
      showTips() {
        this.$refs.tips._show()
      },
      ...mapActions([
        'saveSearchHistory',
        'deleteSearchHistory',
        'clearSearchHistory'
      ])
    }
}
</script>
<style type="text/css">
  .search-history-box{
    margin-top: 10px;
    width: 100%;
    position: absolute;
    bottom: 0;
    top: 200px;
    left: 0;
    right: 0;
  }
  .search-history-list{
    position: absolute;
    overflow: hidden;
    height: 100%;
    top:30px;
    bottom: 0;
    left: 0;
    right: 0;
  }
  .search-history-title{
    width: 100%;
    height: 30px;
    line-height: 30px;
    color: #999;
    font-size: 14px;
    font-weight: 600;
    text-indent: 5px;
    overflow: hidden;
  }
  .search-history-tips{
    width: 100%;
    height: 30px;
    line-height: 30px;
    text-align: center;
    font-size: 13px;
    color: hsla(0,0%,100%,.2);
  }
  .history-icon{
    float: right;
    width: 6%;
    height: 100%;
    font-size: 16px;
    text-indent: 0;
    text-align: center;
    color: hsla(0,0%,100%,.3);
  }
  .searchBody{
    position: absolute;
    top: 88px;
    bottom: 0;
    width: 100%;
    min-width: 320px;
    overflow: hidden;
    background:#222;
    z-index: 10;
  }
  .search-box-postion{
    position: absolute;
    /*width: 90%;*/
    top: 10px;
    left: 5%;
    right: 5%;
    bottom: 0;
  }
  .search-hot-box{
    width: 100%;
    margin-top: 20px;
    /*  max-height: 150px;*/
    height: 150px;
    /*  min-height: 30px;*/
    background: #222;
    overflow: hidden;
  }
  .search-hot-title{
    width: 100%;
    height: 30px;
    line-height: 30px;
    margin-bottom: 10px;
    color: #999;
    font-size: 14px;
    font-weight: 600;
    text-indent: 5px;
    overflow: hidden;
  }
  .hot-key{
    float: left;
    max-width: 17%;
    margin: 5px 2%;
    padding: 0 2%;
    border-radius: 8px;
    height: 25px;
    line-height: 25px;
    font-size: 14px;
    color: hsla(0,0%,100%,.3);
    font-weight: 400;
    background: #333;
    overflow: hidden;
  }
  .no-hotkey{
    width: 100%;
    height: 80px;
    font-size: 16px;
    color: hsla(0,0%,100%,.1);
    text-align: center;
    line-height: 80px;
  }
  .search-query-box{
    position: fixed;
    bottom: 0;
    top: 160px;
    left: 0;
    right: 0;
    z-index: 10;
    overflow: hidden;
    background: #222;
  }
  .refresh-box {
    width: 100%;
    height: 60px;
    overflow: hidden;
    text-align: center;
    line-height: 60px;
    font-size: 14px;
    color: hsla(0,0%,100%,.5);
  }
  .tips-text{
    display: inline-block;
    width: 100%;
    height: 90px;
    text-align: center;
    line-height: 100px;
    color: hsla(0,0%,100%,.5);
    font-size: 15px;
  }
</style>


