<template>
<div class="seach-box">
  <span class="seach-icon"><i class="iconfont icon-sousuo"></i></span>
  <input type="text" class="seach-input" v-model="query" :placeholder="placeholder" ref="query">
  <span class="seach-icon" @click="clear"><i class="iconfont icon-quxiao" v-show="query !== ''"></i></span>
</div>
</template>
<script type="text/javascript">
  import {debounce} from 'common/js/util'

  export default {
    props: {
      placeholder: {
        type: String,
        default: ','
      }
    },
    data() {
      return {
        query: ''
      }
    },
    methods: {
      clear() {
        this.query = ''
      },
      setQuery(query) {
        this.query = query
      },
      _blur() {
        this.$refs.query.blur()
      }
    },
    created() {
      this.$watch('query', debounce((newQuery) => {
        this.$emit('query', newQuery)
      }, 300))
    }
  }
</script>
<style scoped>
  .seach-box{
    width: 100%;
    height: 40px;
    background: #333;
    border-radius: 5px;
    overflow: hidden;
  }
  .seach-input{
    float: left;
    height: 20px;
    width: 70%;
    /*padding: 0 5%;*/
    line-height: 20px;
    /*padding: 0 5px;*/
    margin: 10px 0;
    background: #333;
    color: #fff;
    font-size: 14px;
    outline: none;
    border: none;
    text-align: left;
  }
  .seach-icon{
    float: left;
    /*width: 30px;*/
    width: 15%;
    height: 40px;
    line-height: 40px;
    text-align: center;
  }
  .seach-icon i{
    font-size: 18px;
    color: #222;
  }
</style>


