<template>
  <div ref="wrap" class="wrap" @touchstart="_touchStart($event)" @touchmove="_touchMove($event)" @touchend="_touchEnd($event)">
    <div ref="scrollItem" style="transform:translate3d(0px,0px,0px);" class="scrollItem">
     <slot></slot>
   </div>
 </div>
</template>
<script type="text/javascript">
//  scroll:v-on:updateY _slide(time, Y, getY, all)  time msYpx
//  getY Boolean,(updateY), all Boolean,(Yfalse
const END_REFRESH_TIME = 250
export default {
  props: {
    inhibitMove: {
      type: [Boolean],
      default: false
    }
  },
  data() {
    return {
    }
  },
  created() {
    this.$nextTick(() => {
      this._init()
    })
  },
  mounted() {
    // window.requestAnimationFrame(this._init)
  },
  methods: {
    // 
    _init() {
      this.scrollItem = this.$refs.scrollItem
      if (this.scrollItem) {
        // 
        this.scrollItemH = parseInt(this.scrollItem.offsetHeight)
        this.wrapH = parseInt(this.$refs.wrap.offsetHeight)
        // 
        this.differH = parseInt(this.wrapH - this.scrollItemH)
        this.computedStyle = window.getComputedStyle(this.scrollItem)
        // window.addEventListener("touchstart", func, {passive: true})
      }
    },
    _getTranslateY(endY) {
      let translateY = parseInt(this.computedStyle.getPropertyValue('transform').toString().slice(22, -1))
      this.$emit('updateY', translateY)
      if (endY === translateY) {
        return
      } else {
        window.requestAnimationFrame(this._getTranslateY)
      }
    },
    _touchStart (e) {
      if (this.inhibitMove) {
        return
      }
      if (this.scrollItem) {
        // 
        this.translateY = parseInt(this.computedStyle.getPropertyValue('transform').toString().slice(22, -1))
        // translate
        this.scrollItem.style.transform = 'translate3d(0,' + this.translateY + 'px, 0)'
        this.scrollItem.style.transition = ''
        this.markY = this.translateY
        //  
        if (this.scrollItemH !== parseInt(this.scrollItem.offsetHeight)) {
          this.scrollItemH = parseInt(this.scrollItem.offsetHeight)
          this.wrapH = parseInt(this.$refs.wrap.offsetHeight)
          this.differH = this.wrapH - this.scrollItemH
        }
        this.clickY = e.touches[0].clientY
        this.nowY = this.clickY
        this.beforeEndY = this.nowY
        // this.$refs.wrap.style.overflow = 'scroll'
        this.$emit('beforeScroll')
        clearTimeout(this.timer)
        this.timer = setInterval(() => {
          this.beforeEndY = this.nowY
        }, END_REFRESH_TIME)
      }
    },
    _move(e) {
      e.preventDefault()
      e.stopPropagation()
      if (this.inhibitMove) {
        return
      }
      this.nowY = e.touches[0].clientY
      this.translateY = e.touches[0].clientY - this.clickY + this.markY
      if (this.translateY < this.differH) {
        if (this.differH >= 0) {
          // let H = this.translateY / 3
          this.scrollItem.style.transform = 'translate3d(0,' + this.translateY / 3 + 'px, 0)'
          this.$emit('updateY', this.translateY / 3)
          return
        }
        let H = parseInt(this.differH + (this.translateY - this.differH) / 2.5)
        this.scrollItem.style.transform = 'translate3d(0,' + H + 'px, 0)'
        this.$emit('updateY', H)
        return
      }
      if (this.translateY <= 0 && this.differH < 0) {
        this.scrollItem.style.transform = 'translate3d(0,' + this.translateY + 'px, 0)'
        this.$emit('updateY', this.translateY)
        return
      }
      if (this.translateY > 0) {
        let H = this.translateY / 3
        this.scrollItem.style.transform = 'translate3d(0,' + H + 'px, 0)'
        this.$emit('updateY', H)
        return
      }
    },
    _touchMove(e) {
      window.requestAnimationFrame(() => {
      // if (moveTimer) {
      //   clearTimeout(moveTimer)
      // }
      // let moveTimer = setTimeout(() => {
        this._move(e)
      // }, 100)
      })
    },
    _touchEnd(e) {
      if (this.inhibitMove) {
        return
      }
      // this.$refs.wrap.style.overflow = 'hidden'
      // end
      this.endY = e.changedTouches[0].clientY
      this.distance = parseInt(this.endY) - parseInt(this.beforeEndY)
      // 
      clearInterval(this.timer)
      this.timer = null
      // 
      this.translateY = parseInt(this.scrollItem.style.transform.toString().slice(17, -8))
      this.$emit('updateY', this.translateY)
       //  
      if (this.distance === 0 && this.translateY === 0) {
        return
      }
      if (this.translateY > 0) {
        this._ease(400, 0)
        return
      }
      //  
      if (this.translateY < this.differH) {
        // if (this.distance === 0) {
        //   return
        // }
        if (this.differH >= 0) {
          this._ease(400, 0)
          return
        }
        this._ease(400, this.differH)
        return
      }
      if (Math.abs(this.distance) > 40 && this.translateY < 0 && this.translateY > this.differH) {
        let time = this.distance * 8
        if (time < 0) {
          time = -time
        }
        if (time > 1500) {
          time = 1500
        }
        if (Math.abs(this.distance) < 80) {
          this.distance = this.distance / 10
        }
        if (Math.abs(this.distance) >= 80 && Math.abs(this.distance) < 100) {
          this.distance = this.distance / 2
        }
        if (Math.abs(this.distance) >= 100 && Math.abs(this.distance) < 130) {
          this.distance = this.distance / 1.5
        }
        let X = this.distance * 7
        let Y = this.translateY + X
        if (Y > 0) {
          time = (-this.translateY) * time / X
          Y = 0
        }
        if (Y < this.differH) {
          time = (this.differH - this.translateY) * time / X
          Y = this.differH
        }
        this._slide(time, Y, true, false)
        return
      }
    },
    _ease (time, Y) {
      window.requestAnimationFrame(() => {
        this.scrollItem.style.transition = '-webkit-transform ' + time + 'ms ease 0s'
        this.scrollItem.style.transform = 'translate3d(0px,' + parseInt(Y) + 'px,0px)'
        this._getTranslateY(parseInt(Y))
      })
    },
    _slide (time, Y, getY, all) {
      if (all && !Y) {
        window.requestAnimationFrame(() => {
          if (time) {
            this.scrollItem.style.transition = '-webkit-transform ' + time + 'ms cubic-bezier(0.333333, 0.666667, 0.666667, 1) 0s'
          } else {
            this.scrollItem.style.transition = ''
          }
          this.scrollItem.style.transform = 'translate3d(0px,' + this.differH + 'px,0px)'
          if (getY) {
            this._getTranslateY(this.differH)
          }
          return
        })
      }
      window.requestAnimationFrame(() => {
        if (time) {
          this.scrollItem.style.transition = '-webkit-transform ' + time + 'ms cubic-bezier(0.333333, 0.666667, 0.666667, 1) 0s'
        } else {
          this.scrollItem.style.transition = ''
        }
        this.scrollItem.style.transform = 'translate3d(0px,' + parseInt(Y) + 'px,0px)'
        if (getY) {
          this._getTranslateY(parseInt(Y))
        }
      })
    }
  },
  destroyed() {
    clearInterval(this.timer)
  }
}
</script>
<style scoped>
  .wrap{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    touch-action: none;
  }
</style>


