<template>
  <transition name="tips">
  <div class="center-tips" v-show="showTips">
    <slot></slot>
    <div class="tips-footer">
      <div class="footer-item" @click="_sure">
        
      </div>
      <div class="footer-item" @click="_cancel">
        
      </div>
    </div>
  </div>
</transition>
</template>
<script type="text/javascript">
  export default{
    data() {
      return {
        showTips: false
      }
    },
    methods: {
      _show() {
        this.showTips = true
      },
      _sure() {
        this.$emit('sure')
        this.showTips = false
      },
      _cancel() {
        // this.$emit('cancel')
        this.showTips = false
      }
    }
  }
</script>
<style scoped>
  .tips-enter-active, .tips-leave-active{
    transition: all .3s;
    opacity: 1;
  }
  .tips-enter, .tips-leave-to{
    transition: all .3s;
    opacity: 0;
  }
  .center-tips{
    width: 76%;
    height: 140px;
    position: fixed;
    top: 50%;
    left: 50%;
    z-index: 25;
    transform: translate(-50%,-50%);
    background: #333;
    border-radius: 10px;
  }
  .tips-footer{
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 40px;
  }
  .footer-item{
    /*display: inline-block;*/
    float: left;
    width: 50%;
    height: 100%;
    text-align: center;
    line-height: 40px;
    color: #999;
    font-size: 14px;
    box-sizing: border-box;
    border-top: 1px solid #444;
  }
  .footer-item:first-child{
    border-right: 1px solid #444;
  }
</style>


