<template>
	<div>
		<ul>
		<router-link tag='li' class='nav-li' to='/recommend'><i></i></router-link>
		<router-link tag='li' class='nav-li' to='/singer'><i></i></router-link>
		<router-link tag='li' class='nav-li' to='/rank'><i></i></router-link>
		<router-link tag='li' class='nav-li' to='/search'><i></i></router-link>
		</ul>
	</div>
</template>
<script>
	export default{
	}
</script>
<style scoped>
ul{
	position: fixed;
	top: 44px;
	left: 0;
	width: 100%;
	min-width: 320px;
	height: 45px;
	line-height: 45px;
	background: #222;
	overflow: hidden;
	z-index: 3;
}
.nav-li{
	width: 25%;
	height: 100%;
	text-align: center;
	line-height: 40px;
	font-size: 14px;
	color: #999;
	float: left;
	overflow: hidden;
	/*background: #222;*/
}
.router-link-active{
	color: #ffcd32;
}
.router-link-active i{
	display: block;
	width: 33%;
	height: 2px;
	margin: -.4rem auto 0;
	background-color: #ffcd32;
}
</style>


