<template>
  <transition name="tips">
    <div class="titpsBody" @click="_close" v-show="open">
      <slot></slot>
    </div>
  </transition>
</template>
<script type="text/javascript">
export default {
  props: {
    showTips: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      open: false
    }
  },
  methods: {
    _close() {
      // this.$emit('close')
      this.open = false
    },
    _open() {
      this.open = true
      if (this.time) {
        clearTimeout(this.time)
      }
      this.time = setTimeout(() => {
        this.open = false
      }, 1000)
    }
  }
}
</script>
<style scoped>
.tips-enter-active, .tips-leave-active{
  transition: all .3s;
  transform: translate3d(0,0,0);
  opacity: 1;
}
.tips-enter, .tips-leave-to{
  transition: all .3s;
  transform: translate3d(0,-100%,0);
  opacity: 0;
}
.titpsBody{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 25;
  height: 40px;
  width: 100%;
  background: #444;
}
</style>


