<template>
	<div v-show="searches.length" class="searche-list">
    <v-scroll ref="scroll">
      <ul class="searche-scroll-ul">
       <li class="searche-item" v-for="(item,index) in searches" :class="{searchePlayList:item.id && item.name}">
        <span class="searche-bofang" v-if="item.id && item.name"><i class="iconfont icon-bofang searche-bofang-icon" v-show="searcheIndex === index"></i></span>
        <span class="searche-text ellipsis" @click.stop.prevent="selectItem(item,index)">{{ item.id && item.name ? `${item.name} - ${item.singer}` : item }}</span>
        <span class="iconfont icon-quxiao1 searche-icon" @click.stop.prevent="deleteThis(item, index)"></span>
        <span class="iconfont icon-aixin searche-icon" v-show="item.id && item.name"></span>
      </li>
    </ul>
  </v-scroll>
</div>
</template>
<script type="text/javascript">
  import VScroll from 'base/scroll/scroll'
  const ITEM_HEIGHT = 35
  export default {
    props: {
      searches: {
        type: Array,
        default: []
      },
      searcheIndex: {
        type: Number,
        default: 0
      }
    },
    components: {
      VScroll
    },
    methods: {
      selectItem(item, index) {
        // console.log(item)
        this.$emit('select', item, index)
      },
      deleteThis(item, index) {
        this.$emit('delete', item, index)
      },
      _loction() {
        if (this.searcheIndex * ITEM_HEIGHT < this.$refs.scroll.wrapH) {
          // const Y = -this.searcheIndex * ITEM_HEIGHT
          this.$refs.scroll._slide(500, 0)
        } else {
          const Y = Math.max(-(this.searcheIndex * ITEM_HEIGHT), this.$refs.scroll.differH)
          this.$refs.scroll._slide(500, Y)
        }
      }
    },
    watch: {
      searcheIndex() {
        this.$nextTick(() => {
          this._loction()
        })
      }
    }
  }
</script>
<style scoped>
  .searche-list{
   width: 100%;
   height: 100%;
   touch-action: none;
 }
 .searche-item{
  width: 100%;
  height: 35px;
  line-height: 35px;
  text-align: center;
  color: hsla(0,0%,100%,.3);
}
.searche-icon{
  float: right;
  width: 6%;
  height: 100%;
  font-size: 18px;
}
.searche-text{
  float: left;
  width: 60%;
  height: 100%;
  text-align: left;
  text-indent: 5px;
}
.searche-bofang{
  width: 10%;
  float: left;
  height: 100%;
  text-align: center;
}
.searchePlayList{
  padding-right: 15px;
}
.searche-bofang-icon{
  font-size: 14px;
  color: #ffcd32;
}
.searchePlayList .searche-icon{
  width: 9%;
  font-size: 14px;
  color: #ffcd32;
}
.searche-scroll-ul{
  padding-bottom: 40px;
}
</style>


