<template>
  <div ref="slider" class="slider">
    <div class="sliderBody" ref="sliderGroup" style="transform: translate3d(0px,0px,0px);" @touchstart="_touchStart($event)"@touchmove.stop.prevent="_touchMove($event)" @touchend="_touchEnd($event)">
      <slot></slot>
    </div>
  </div>
</template>
<script>
// sliderNowPage
  export default{
    props: {
      sliderW: {
        type: [Number, Boolean],
        default: false
      },
      sliderH: {
        type: [Number, Boolean],
        default: false
      },
      autoPaly: {
        type: [Number, Boolean],
        default: 4500
      },
      moveTime: {
        type: [Number, Boolean],
        default: 600
      }
    },
    data() {
      return {
        nowPage: 0
      }
    },
    created() {
      // this.dots = []
      this.translateX = 0
      let that = this
      window.addEventListener('optimizedResize', function() {
        requestAnimationFrame(() => {
          that._setSliderWidth()
        })
      })
    },
    mounted() {
      this.$nextTick(() => {
        this._resizeChange()
        this._setSliderWidth()
      })
    },
    methods: {
      _resizeChange() {
        let throttle = function(type, name, obj) {
          obj = obj || window
          let running = false
          let func = function() {
            if (running) {
              return
            }
            running = true
            requestAnimationFrame(function() {
              obj.dispatchEvent(new CustomEvent(name))
              running = false
            })
          }
          obj.addEventListener(type, func)
        }
        throttle('resize', 'optimizedResize')
      },
      _setSliderWidth() {
        this.nowPage = 0
        this._resetWidth()
        this.group = this.$refs.sliderGroup
        if (this.group.children) {
          this.children = this.group.children
        }
        this.length = this.children.length
        const allW = this.sliderWidth * this.length
        this.group.style.width = `${allW}px`
        this._setGroupStly()
        this._setChildStyle()
      },
      _setChildStyle() {
        if (this.length > 0) {
          for (let i = 0; i < this.length; i++) {
            let child = this.children[i]
            child.style.width = this.sliderWidth + 'px'
            let className = child.getAttribute('class')
            if (className === null) {
              child.setAttribute('class', 'sliderItem')
            } else {
              if (className.indexOf('sliderItem') < 0) {
                className = `${className} sliderItem`
                child.setAttribute('class', className)
              }
            }
          }
        }
      },
      _setGroupStly() {
        if (this.autoPaly) {
          this.group.style.transform = 'translate3d(' + (-this.sliderWidth) + 'px,0px,0px)'
          this._autoPaly()
        } else {
          this.group.style.transform = 'translate3d(0px,0px,0px)'
        }
      },
      _resetWidth() {
        if (this.sliderW) {
          this.$refs.slider.style.width = `${this.sliderW}px`
          this.sliderWidth = this.sliderW
        } else {
          this.sliderWidth = parseInt(this.$refs.slider.clientWidth)
        }
        if (this.sliderH) {
          this.$refs.slider.style.height = `${this.sliderH}px`
        } else {
          this.$refs.slider.style.height = this.sliderWidth / 2.5 + 'px'
        }
      },
      _touchStart(e) {
        this.group.style.transition = ''
        this.translateX = parseInt(this.group.style.transform.toString().slice(12, -13))
        this.startX = e.touches[0].clientX
        if (this.autoPaly) {
          clearInterval(this.play)
          this.play = null
          if (this.translateX === 0) {
            this.translateX = this.sliderWidth * (2 - this.length)
            this.group.style.transform = 'translate3d(' + this.translateX + 'px,0px,0px)'
            return
          }
          if (this.translateX === this.sliderWidth * (1 - this.length)) {
            this.translateX = -this.sliderWidth
            this.group.style.transform = 'translate3d(' + this.translateX + 'px,0px,0px)'
            return
          }
        }
      },
      _touchMove(e) {
        let that = this
        requestAnimationFrame(() => {
          e.preventDefault()
          e.stopPropagation()
          that.nowX = e.touches[0].clientX
          let move = that.translateX + that.nowX - that.startX
          if (move < 0 && move > this.sliderWidth * (1 - this.length)) {
            that.group.style.transform = 'translate3d(' + move + 'px,0px,0px)'
          }
        })
      },
      _touchEnd(e) {
        e.stopPropagation()
        this.group.style.transition = `${this.moveTime}ms cubic-bezier(0.165, 0.84, 0.44, 1) 0s`
        requestAnimationFrame(() => {
          this._autoPaly()
          if (parseInt(this.nowX - this.startX) > 0) {
            // 
            if (Math.abs(this.nowX - this.startX) > 80 && this.translateX < 0) {
              const Y = this.translateX + this.sliderWidth
              this.group.style.transform = `translate3d(${Y}px,0px,0px)`
              this.nowPage--
              if (this.autoPaly) {
                if (this.nowPage < 0) {
                  this.nowPage = this.length - 3
                }
              }
              return
            } else {
              this.group.style.transform = 'translate3d(' + this.translateX + 'px,0px,0px)'
              return
            }
          } else {
            // 
            if (Math.abs(this.nowX - this.startX) > 80 && this.translateX > -this.sliderWidth * (this.length - 1)) {
              const Y = this.translateX - this.sliderWidth
              this.group.style.transform = `translate3d(${Y}px,0px,0px)`
              this.nowPage++
              if (this.autoPaly) {
                if (this.nowPage > this.length - 3) {
                  this.nowPage = 0
                }
              }
              return
            } else {
              this.group.style.transform = 'translate3d(' + this.translateX + 'px,0px,0px)'
              return
            }
          }
        })
      },
      _autoPaly() {
        if (!this.autoPaly) {
          return
        }
        clearInterval(this.play)
        this.play = setInterval(() => {
          this.translateX = parseInt(this.group.style.transform.toString().slice(12, -13))
          if (this.translateX <= parseInt(this.sliderWidth * (1 - this.length))) {
            this.group.style.transition = ''
            this.translateX = -this.sliderWidth
            this.group.style.transform = 'translate3d(' + this.translateX + 'px,0px,0px)'
            this.nowPage = 0
            return
          } else {
            this.group.style.transition = '.6s cubic-bezier(0.165, 0.84, 0.44, 1) 0s'
            this.translateX = parseInt(this.translateX - this.sliderWidth)
            this.group.style.transform = 'translate3d(' + this.translateX + 'px,0px,0px)'
            this.nowPage++
            if (this.nowPage === this.length - 2) {
              this.nowPage = 0
            }
            return
          }
        }, this.autoPaly)
      }
    },
    watch: {
      sliderW() {
        this._setSliderWidth()
      },
      sliderH() {
        this._setSliderWidth()
      },
      nowPage() {
        requestAnimationFrame(() => {
          this.$emit('sliderNowPage', this.nowPage)
        })
      }
    },
    destroyed() {
      clearInterval(this.play)
    }
  }
</script>
<style scoped>
  .slider{
    width: 100%;
    overflow: hidden;
    position: relative;
  }
  .sliderBody{
    display: inline-block;
    white-space: nowrap;
    width: 100%;
    height: 100%;
    font-size: 0;
    overflow: hidden;
  }
  .sliderItem{
    float: left;
    height: 100%;
  }
  .sliderItem a{
    display: block;
    width: 100%;
    height: 100%
  }
  .sliderItem a img{
    width: 100%;
    height: 100%;
  }
</style>


