<template>
  <div class="all-palyer" v-show="playlist.length>0">
    <transition name="normal">
      <div class="normal-palyer" v-show="fullScreen">
        <img class="normal-palyer-cover" :src="currentSong.image"></img>
        <header class="header" name="header">
          <h1 class="palyer-name ellipsis"><span class="iconfont icon-down normal-palyer-icon" @click.capture="_narrow()"></span><span v-html="currentSong.name" class="name-span ellipsis"></span></h1>
          <h2 class="author ellipsis" v-html="currentSong.singer"></h2>
        </header>
        <div class="palyer-middel" ref="palyerMiddel">
          <Slider :sliderH="palyerMiddelH" :sliderW="palyerMiddelW" :autoPaly="false" :moveTime="400" ref="Mslider" v-on:sliderNowPage="_updateNowPage">
            <div class="palyer-middel-item">
              <div class="palyer-cd-wrap">
               <div class="palyer-cd" :style="bgStyle" ref="normalImg">
                <!-- <img src="https://y.gtimg.cn/music/photo_new/T002R300x300M000003y8dsH2wBHlo.jpg?max_age=2592000"> -->
              </div>
            </div>
            <div class="palyer-cd-lyric">
              <p class="cd-lyric-p ellipsis">{{ playingLyric }}</p>
            </div>
          </div>
          <div class="palyer-middel-item">
            <v-scroll ref="palyerScroll" :inhibitMove="true">
              <ul class="palyer-lyric" v-if="currentLyric">
                <li v-for="(item, index) in currentLyric.lines" class="palyer-lyric-item ellipsis" :class="{current:currentLineNum === index}">
                  {{ item.txt }}
                </li>
              </ul>
              <ul class="palyer-lyric" v-if="!currentLyric">
                <li class="palyer-lyric-item ellipsis"></li>
              </ul>
            </v-scroll>
          </div>
        </Slider>
      </div>
      <div class="dotsPostion">
        <v-dots :dots="palyerMiddeLitem" :nowPage="recommendNowPage"></v-dots>
      </div>
      <footer>
        <div class="progress-bar">
          <div class="nowTime">{{ _format(currentTime) }}</div>
          <div class="planned" @click="_progressClick($event)">
            <span class="planned-bg" ref="planned"><span class="has-played" style="width:0%" ref="played">
              <i ref="playedIcon" class="iconfont icon-bofangjindutiao planned-icon" style="transform: translate3d(0,0,0);" @touchstart.stop.prevent="_touchStart($event)" @touchmove.stop.prevent="_touchMove($event)" @touchend.stop.prevent="_touchEnd($event)"></i>
            </span>
          </span>
        </div>
        <div class="totalTime">{{ _format(currentSong.duration) }}</div>
      </div>
      <div class="control clearfix">
       <div class="control-btn" @click="_changeMode()">
        <i class="iconfont control-icon-min" :class="iconMode"></i>
      </div>
      <div class="control-btn">
        <i class="iconfont icon-shangyishou control-icon-min" @click="_prev()" :class="{disable:!readyOk}"></i>
      </div>
      <div class="control-btn">
        <i class="iconfont icon-bofang control-icon-max" :class="{disable:!readyOk}" @click="_toControlPlay()" v-show="!playing"></i>
        <i class="iconfont icon-zanting control-icon-max-ting" :class="{disable:!readyOk}" @click="_toControlPlay()" v-show="playing"></i>
      </div>
      <div class="control-btn">
        <i class="iconfont icon-xiayishou control-icon-min" @click="_next()" :class="{disable:!readyOk}"></i>
      </div>
      <div class="control-btn">
        <i class="iconfont icon-aixin control-icon-min"></i>
      </div>
    </div>
  </footer>
</div>
</transition>
<!--        -->
<!-- playlist.length>0&&!fullScreen -->
<transition name="mini">
  <div class="mini-palyer clearfix" v-show="!fullScreen" @click="_open()">
    <div class="mini-img">
      <img :src="currentSong.image" class="mini-cd" ref="miniImg">
    </div>
    <div class="mini-btn">
      <i class="iconfont icon-bofang control-icon-mini" @click.stop.prevent="_toControlPlay()" v-show="!playing"></i>
      <i class="iconfont icon-zanting control-icon-mini" @click.stop.prevent="_toControlPlay()" v-show="playing"></i>
      <span class="iconfont icon-yinleliebiao control-icon-mini" @click.stop.prevent="_showList()"></span>
    </div>
    <div class="mini-name">
      <span v-html="currentSong.name" class="song-name ellipsis"></span>
      <span v-html="currentSong.singer" class="song-author ellipsis"></span>
    </div>
  </div>
</transition>
<audio :src="currentSong.url" ref="audio" @canplay="_ready" @error="_error" @timeupdate="_updateTime"
@ended="_end"></audio>
<play-list ref="playList"></play-list>
</div>
</template>
<script type="text/javascript">
  import {mapGetters, mapMutations, mapActions} from 'vuex'
  import {playMode} from 'common/js/config'
  import Lyric from 'lyric-parser'
  import VScroll from 'base/scroll/scroll'
  import Slider from 'base/slider/slider'
  import VDots from 'base/dots/dots'
  import PlayList from 'components/playlist/playlist'
  import {playerMixin} from 'common/js/mixin'

  const DOT_HALF_WIDTH = 10
  const LYRIC_LI_HEIGHT = 34
  // const LYRICS_HEIGHT = 30
  // const PLAY_MOVE_TIME = 300

  export default{
    mixins: [playerMixin],
    data() {
      return {
        readyOk: false,
        currentTime: 0,
        initiated: false,
        currentLyric: null,
        currentLineNum: 0,
        totalLineLength: 0,
        playingLyric: ''
      }
    },
    mounted() {
    },
    created() {
      this.palyerMiddelH = 400
      this.palyerMiddelW = 375
      this.recommendNowPage = 0
      this.palyerMiddeLitem = [0, 0]
    },
    computed: {
      bgStyle() {
        return `background-image:url(${this.currentSong.image})`
      },
      ...mapGetters([
        'fullScreen',
        'playing',
        'currentIndex'
      ])
    },
    methods: {
      _init() {
        this.plannedW = this.$refs.planned.clientWidth
        this.playedIcon = this.$refs.playedIcon
        this.played = this.$refs.played
        this.palyerMiddelH = parseInt(this.$refs.palyerMiddel.clientHeight)
        this.palyerMiddelW = parseInt(this.$refs.palyerMiddel.clientWidth)
      },
      _narrow() {
        this.setFullScreen(false)
      },
      _open() {
        this.setFullScreen(true)
      },
      _toControlPlay() {
        if (!this.readyOk) {
          return
        }
        this.setPlayingState(!this.playing)
        if (this.currentLyric) {
          this.currentLyric.togglePlay()
        }
      },
      _next() {
        if (!this.readyOk) {
          return
        }
        if (this.playlist.length === 1) {
          this._loop()
        } else {
          let index = this.currentIndex + 1
          if (index >= this.playlist.length) {
            index = 0
          }
          this.setCurrentIndex(index)
          if (!this.playing) {
            this._toControlPlay()
          }
          this.readyOk = false
        }
      },
      _prev() {
        if (!this.readyOk) {
          return
        }
        if (this.playlist.length === 1) {
          this._loop()
        } else {
          let index = this.currentIndex - 1
          if (index <= -1) {
            index = this.playlist.length - 1
          }
          this.setCurrentIndex(index)
          if (!this.playing) {
            this._toControlPlay()
          }
          this.readyOk = false
        }
      },
      _ready() {
        this.readyOk = true
        this.savePlayHistory(this.currentSong)
      },
      _error() {
        this.readyOk = true
      },
      _updateTime(e) {
        this.currentTime = e.target.currentTime
      },
      // 
      _format(interval) {
        interval = interval | 0
        const minute = interval / 60 | 0
        let second = interval % 60
        if (second < 10) {
          second = '0' + second
        }
        return `${minute}:${second}`
      },
      _changeLength() {
        this._setOffet(this.currentTime / this.currentSong.duration * this.plannedW)
      },
      _touchStart(e) {
        this.initiated = true
        this.startX = e.touches[0].clientX
        this.nowX = this.played.clientWidth
      },
      _touchMove(e) {
        if (!this.initiated) {
          return
        }
        const that = this
        requestAnimationFrame(() => {
          const deltaX = e.touches[0].clientX - that.startX
          const offsetWidth = Math.min(that.plannedW, Math.max(0, that.nowX + deltaX))
          // that.nowX = offsetWidth
          that._setOffet(offsetWidth)
        })
      },
      _touchEnd(e) {
        this.initiated = false
        this._triggrPercent()
      },
      _setOffet(offset) {
        this.playedIcon.style.transform = `translate3d(${offset - DOT_HALF_WIDTH}px,0,0)`
        this.played.style.width = `${offset}px`
      },
      _triggrPercent() {
        // this.readyOk = false
        this.nowX = this.played.clientWidth
        const time = this.currentSong.duration * (this.nowX / this.plannedW)
        this.$refs.audio.currentTime = time
        if (!this.playing) {
          this._toControlPlay()
        }
        if (this.currentLyric) {
          this.currentLyric.seek(1000 * time)
        }
      },
      _progressClick(e) {
        const offset = e.offsetX - DOT_HALF_WIDTH
        this._setOffet(offset)
        this._triggrPercent()
      },
      _end() {
        if (this.mode === playMode.loop) {
          this._loop()
        } else {
          this._next()
        }
      },
      _loop() {
        this.$refs.audio.currentTime = 0
        this.$refs.audio.play()
        this.setPlayingState(true)
        if (this.currentLyric) {
          this.currentLyric.seek(0)
        }
      },
      _getLyric() {
        this.currentSong.getLyric().then((lyric) => {
          if (this.currentSong.lyric !== lyric) {
            return
          }
          this.currentLyric = new Lyric(lyric, this.handleLyric)
          if (this.playing) {
            this.currentLyric.play()
          }
          this.totalLineLength = this.currentLyric.lines.length
        }).catch(() => {
          this.currentLyric = null
          this.playingLyric = ''
          this.currentLineNum = 0
        })
      },
      handleLyric({lineNum, txt}) {
        this.currentLineNum = lineNum
        this.playingLyric = txt
        if (lineNum < 6) {
          return
        }
        const heightLine = 6 * LYRIC_LI_HEIGHT
        const postion = this.currentLineNum * LYRIC_LI_HEIGHT - heightLine
        this.$refs.palyerScroll._slide(1000, -postion)
        // if (lineNum > 5) {
        //   let linEl = this.$refs.
        // }
      },
      _updateNowPage(res) {
        this.recommendNowPage = res
      },
      _showList() {
        this.$refs.playList._close()
      },
      ...mapMutations({
        setFullScreen: 'SET_FULL_SCREEN',
        setPlayingState: 'SET_PLAYING_STATE',
        setPlaylist: 'SET_PLAYLIST'
      }),
      ...mapActions([
        'savePlayHistory'
      ])
    },
    components: {
      VScroll,
      Slider,
      VDots,
      PlayList
    },
    watch: {
      currentSong(newSong, oldSong) {
        if (!newSong.id) {
          return
        }
        if (newSong.id === oldSong.id) {
          return
        }
        this.$refs.palyerScroll._slide(0, 0)
        if (this.currentLyric) {
          this.currentLyric.stop()
          this.currentTime = 0
          this.playingLyric = ''
          this.currentLineNum = 0
        }
        this.$nextTick(() => {
          this.$refs.audio.play()
          this._getLyric()
          if (this.plannedW) {
            return
          }
          this._init()
        })
      },
      playing(newPlaying) {
        const audio = this.$refs.audio
        const that = this
        requestAnimationFrame(() => {
          that.$nextTick(() => {
            if (newPlaying) {
              that.$refs.normalImg.style.animationPlayState = 'running'
              that.$refs.miniImg.style.animationPlayState = 'running'
              audio.play()
            } else {
              that.$refs.normalImg.style.animationPlayState = 'paused'
              that.$refs.miniImg.style.animationPlayState = 'paused'
              audio.pause()
            }
          })
        })
      },
      currentTime() {
        if (!this.plannedW) {
          return
        }
        if (this.initiated) {
          return
        }
        window.requestAnimationFrame(() => {
          this._changeLength()
        })
      }
    }
  }
</script>
<style scoped>
  .palyer-lyric{
    width: 100%;
    padding: 10px 0;
    overflow: hidden;
  }
  .palyer-lyric-item{
    max-width: 90%;
    height: 14px;
    padding: 10px 30px;
    text-align: center;
    font-size: 14px;
    color: hsla(0,0%,100%,.5);
    line-height: 34px;
  }
  .palyer-cd-lyric{
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    font-size: 14px;
    height: 30px;
    line-height: 30px;
  }
  .cd-lyric-p{
    width: 70%;
    color: hsla(0,0%,100%,.5);
    text-align: center;
    margin: 0 auto
  }
  .mini-enter-active, .mini-leave-active{
    transition: all .6s;
    transform: translate3d(0,0,0);
  }
  .mini-enter, .mini-leave-to{
    transition: all .4s;
    transform: translate3d(0,100%,0);
  }
  .normal-enter-active, .normal-leave-active{
    transition: all .6s;
    transform: scale(1,1);
    transform-origin:8% 97%;
    opacity: .9;
  }
  .normal-enter, .normal-leave-to{
    transition: all .6s;
    transform: scale(.1,0);
    transform-origin:8% 97%;
    opacity: 0;
  }
  @keyframes rotate{
    0%{
      transform: rotate(0deg);
    }
    50%{
      transform: rotate(180deg);
    }
    100%{
      transform: rotate(360deg);
    }
  }
  .palyer-cd, .mini-cd{
    -moz-animation: rotate 20s linear infinite;
    -webkit-animation: rotate 20s linear infinite;
    -o-animation: rotate 20s linear infinite;
    animation: rotate 20s linear infinite forwards;
  }
</style>
<style scoped>
  .current{
    color: #fff;
  }
  .mini-palyer{
    position: fixed;
    height: 60px;
    bottom: 0;
    left: 0;
    right: 0;
    background: #222;
    background-color: #333;
    z-index: 10;
    overflow: hidden;
    min-width: 320px;
    opacity: .95;
  }
  .mini-img{
    float: left;
    width: 20%;
    min-width: 60px;
    background: #333;
    overflow: hidden;
  }
  .mini-img img{
    display: block;
    margin: 10px auto;
    width: 40px;
    height: 40px;
    border-radius: 50%;
  }
  .mini-nam{
    width: 40%;
    font-size: 14px;
    color: #fff;
    background: #333;
    overflow: hidden;
  }
  .song-name, .song-author{
  }
  .song-name{
    display: block;
    height: 20px;
    line-height: 20px;
    margin-top: 15px;
    font-size: 14px;
    color: #fff;
  }
  .song-author{
    display: inline-block;
    height: 20px;
    line-height: 20px;
    font-size: 12px;
    color: hsla(0,0%,100%,.3);
  }
  .mini-btn{
    float: right;
    width: 25%;
    min-width: 80px;
    background: #333;
    line-height: 60px;
  }
  .mini-btn i,.mini-btn span{
    display: inline-block;
    width: 30px;
    margin-left: 10%;
  }
</style>
<style scoped>
  .normal-palyer{
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: #222;
    z-index: 10;
    min-width: 320px;
  }
  .normal-palyer-cover{
    position: relative;
    width: 100%;
    height: 100%;
    background:#fff;
    opacity: .5;
    z-index: 5;
    filter: blur(20px);
  }
  .normal-palyer .header{
    position: absolute;
    width: 100%;
    top: 0;
    height: 60px;
    text-align: center;
    z-index: 10;
  }
  .header h1{
    width: 100%;
    height: 40px;
    line-height: 40px;
    text-align: center;
  }
  .palyer-name{
    text-indent: 0;
    font-size: 18px;
    color: #fff;
  }
  .normal-palyer-icon{
    display: inline-block;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 10;
    width: 60px;
    text-align: center;
    font-size: 20px;
    font-weight: 800px;
    color: #ffcd32;
  }
  .name-span{
    max-width: 100%;
  }
  .author{
  	width: 100%;
  	height: 20px;
  	line-height: 20px;
  	font-size: 14px;
  	color: #fff;
  }
  footer{
  	position: fixed;
  	bottom: 0;
  	height: 100px;
  	width: 100%;
    z-index: 10;
  }
  .palyer-middel-item{
    position: relative;
  }
  .dotsPostion{
    position: absolute;
    left: 0;
    right: 0;
    bottom: 110px;
    z-index: 10;
  }
  .palyer-middel{
  	position: fixed;
  	top: 70px;
    bottom: 130px;
    left: 0;
    right: 0;
    z-index: 10;
    overflow: hidden;
  }
  .palyer-cd-wrap{
  	position: relative;
    left: 50%;
    top: 45%;
    transform: translate(-50%, -50%);
    z-index: 10;
    width: 72%;
    height: 0;
    padding-bottom: 72%;  	
    font-size: 0;
    border-radius: 50%;
    background: hsla(0,0%,100%,.1);
    overflow: hidden;
    box-shadow: 0px 0px 10px hsla(0,0%,100%,.1);
    border: 10px solid hsla(0,0%,100%,.1);
    text-align: center;
  }
  .palyer-cd{
  	display: inline-block;
  	width: 100%;
  	height: 0;
  	border-radius: 50%;
  	margin: 0 auto;
  	font-size: 0;
  	padding-bottom: 100%;
  	background: #222;
  	overflow: hidden;
  	background-size: contain;
  	transform: rotate(0deg);
  	box-sizing: border-box;
    background-repeat: no-repeat;
  }
  .progress-bar{
  	height: 30px;
  	width: 100%;
    line-height: 30px;
    color: #fff;
    font-size: 12px;
  }
  .has-played{
    position: absolute;
    left: 0;
    top: 0;
    display: inline-block;
    height: 4px;
    height: 100%;
    background: #ffcd32;
  }
  .planned-icon{
    position: absolute;
    left: 0;
    top: 3px;
    display: inline-block;
    line-height: 0;
    font-size: 20px;
    color: #ffcd32;
  }
  .totalTime{
    float: left;
    width: 17%;
    height: 100%;
    text-align: left;
    color: #fff;
    font-size: 12px;
    padding-left: 3%;
  }
  .planned{
    float: left;
    width: 60%;
    height:30px;
    margin: 0 auto;
    /*position:relative;*/
  }
  .planned-bg{
    display: inline-block;
    width: 100%;
    height: 4px;
    margin: 13px auto;
    background: rgba(0,0,0,.3);
    position: relative;
  }
  .nowTime{
    float: left;
    width: 17%;
    height: 100%;
    text-align: right;
    color: #fff;
    font-size: 12px;
    padding-right: 3%;
  }
  .control{
  	width: 100%;
  	height: 60px;
  	padding-bottom: 10px;
  	overflow: hidden;
  }
  .control-btn{
  	height: 100%;
  	width: 20%;
  	margin: 0;
  	padding: 0;
  	line-height: 60px;
  	float: left;
  	border: none;
  	box-sizing: border-box;
  	text-align: center;
  	overflow: hidden;
  }
  .control-icon-max{
    font-size: 38px;
    color: #ffcd32;
  }
  .control-icon-max-ting{
    font-size: 37.45px;
    color: #ffcd32;
  }
  .control-icon-min{
  	font-size: 25px;
  	color: #ffcd32;
  }
  .control-icon-mini{
    font-size: 27px;
    color: #ffcd32;
  }
  .control-btn .disable{
    color: #999;
  }
</style>


