<template>
	<div class="dotsBox" ref="dotsBox">
		<span v-for="(item, index) in dots" class="dot" :class="{dotActive:nowPage === index}"></span>
	</div>
</template>
<script type="text/javascript">
export default{
  props: {
    dots: {
      dafault: []
    },
    nowPage: {
      type: [Number, String],
      dafault: 0
    }
  },
  mounted() {
  }
}
</script>
<style scoped>
  .dotsBox{
    width: 100%;
    height: 8px;
    -webkit-transform: translateZ(1px);
    transform: translateZ(1px);
    text-align: center;
    font-size: 0;
    overflow: hidden;
    opacity: .8;
  }
  .dot{
    display: inline-block;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    margin: 0 4px;
    background: #999;
    opacity: .9;
  }
  .dotActive{
    background: #fff;
    border-radius: 5px;
    width: 20px;
    margin: 0 2px;
  }
</style>


